* {
  font-family: "Roboto", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body,
.App {
  padding: 0;
  margin: 0;
}

header {
  height: 50px;
  font-family: "Style Script", cursive;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

.container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5px;
}

.content {
  width: inherit;
  max-width: 500px;
  margin-bottom: 20px;
}
.top {
  display: flex;
  justify-content: space-between;
  height: 50px;
  padding: 10px;
  align-items: center;
  color: rgba(0, 0, 0, 0.3);
}

.content img {
  border: 1px solid rgba(0, 0, 0, 0.1);
  width: 100%;
  border-radius: 5px;
}

.btns {
  display: grid;
  padding: 10px;
  grid-template-columns: 2.5rem 2.5rem 2.5rem 1fr;
  font-size: 1.5rem;
}

.btns * {
  cursor: pointer;
}
.save {
  text-align: right;
}
.text {
  padding: 0 10px;
}

/* Footer */
footer {
  position: fixed;
  width: 100%;
  bottom: 0;
}

.addbtn {
  bottom: 0;
  height: 50px;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  position: fixed;
  width: 100%;
  display: flex;
  background-color: white;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
  cursor: pointer;
}

/* FILE UPLOAD */

.addimg {
  z-index: 1;
  top: 0;
  position: fixed;
  display: none;
  background-color: rgba(0, 0, 0, 0.8);
  height: 100vh;
  width: 100%;
}
.show {
  display: block;
}

/* FORM */

.form {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
}

.close {
  width: 100%;
  text-align: right;
  color: white;
  font-size: 2rem;
  cursor: pointer;
}

.form-box {
  height: 100%;
  display: flex;
  align-items: center;
}

form {
  background-color: white;
  height: 300px;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  padding: 20px;
}

input[type="file"] {
  height: 100%;
}

input[type="file"]::before {
  content: " ";
}

input[type="file"]::file-selector-button {
  background-color: #0095f6;
  border-radius: 5px;
  font-weight: 500;
  width: 100%;
  padding: 10px;
  color: white;
  border: none;
  content: "hahah";
  cursor: pointer;
  display: flex;
  justify-content: center;
  text-align: center;
}

textarea {
  border: 1px solid rgba(0, 0, 0, 0.1);
  height: 100%;
  color: rgba(0, 0, 0, 0.5);
}

textarea:focus {
  outline: none;
  color: black;
}

button {
  border-radius: 5px;
  font-weight: 500;
  margin-top: 10px;
  color: #0095f6;
  border: none;
  background-color: transparent;
  cursor: pointer;

  padding: 5px;
}

button:hover {
  color: darkblue;
}
